<template>
  <main>
    <div
      class="tab-pane fade show active"
      id="v-pills-news"
      role="tabpanel"
      aria-labelledby="v-pills-news-tab"
    >
      <div class="beige-bg rounded-3">
        <div class="page-scroll L-scrollbar" id="allNews">
          <template v-for="news in news_list_c" :key="news.news_id">
            <div class="row">
              <a
                data-bs-toggle="collapse"
                :href="'#collapseNews' + news.news_id"
                role="button"
                aria-expanded="false"
                :aria-controls="'collapseNews' + news.news_id"
                class="news-index"
              >
                <div class="col news-bg bg-light rounded-3">
                  <div class="news-title title mb-1">
                    <span class="overflow-text text">{{ news.subject }}</span>
                    <span>{{ news.date_start }}</span>
                  </div>
                  <span class="overflow-text text">{{ news.content_c }}</span>
                </div>
              </a>
              <div
                class="collapse collapse-transform"
                :id="'collapseNews' + news.news_id"
              >
                <div
                  class="news-content news-content-bg bg-white text"
                  v-html="news.content"
                ></div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
// const axios = require("axios");
// const $ = require("jquery");
import axios from "../../axios/api";
export default {
  data() {
    return {
      c_jwt: this.jwt,
      news_list: [],
    };
  },
  async mounted() {
    //document.getElementsByClassName("vue-loaders")[0].hidden = false;
    const axiosConfig = {
      method: "get",
      url: "https://www.tkbgo.com.tw/api/news/getNewsList",
      headers: { "x-access-token": this.jwt },
    };
    try {
      const response = await axios(axiosConfig, this);
      const json = await response.data;
      //const { data } = json;
      console.log(json);
      this.news_list = this.news_list.concat(this.news_list, json);
      //document.getElementsByClassName("vue-loaders")[0].hidden = true;
    } catch (err) {
      console.log(err);
    }
    /*
    $("#allNews").on("click", function () {
      $(".collapse").removeClass("show");
    });
    */
  },
  computed: {
    news_list_c() {
      let list = this.news_list;
      list.forEach((i) => {
        i.content_c = i.content.replace(/<[^>]+>/g, "");
      });
      return list;
    },
  },
};
</script>

