import axios from "axios";
import { ipcRenderer } from "electron";
var vue;
var axiosConfig;
var reSendCount = 0;
const baseURL = "https://www.tkbgo.com.tw/api/";
//const baseURL = "https://localhost/api";
const instance = axios.create({
  baseURL: baseURL,
  headers: { "Content-Type": "application/json" },
  timeout: 20000,
});

// 此處的instance為我們create的實體
instance.interceptors.response.use(
  function(response) {
    // Do something with response data
    return response;
  },
  async function(error) {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          console.log(reSendCount);
          if (reSendCount < 2) {
            reSendCount += 1;
            await reAuth();
            return reSend();
          }
          break;
        // go to 404 page
        case 404:
          console.log("你要找的頁面不存在");
          // go to 404 page
          break;
        case 500:
          console.log("程式發生問題");
          // go to 500 page
          break;
        default:
          console.log(error.message);
      }
    }
    if (!window.navigator.onLine) {
      alert("網路出了點問題，重新啟動程式");
      return;
    }
    return Promise.reject(error);
  }
);

function reSend() {
  console.log("重送一次請求");
  return new_axios(axiosConfig, vue);
}

async function reAuth() {
  const axiosConfig = {
    method: "get",
    url: "https://www.tkbgo.com.tw/api/member/auth",
    headers: {
      // remove headers
    },
    auth: {
      username: ipcRenderer.sendSync("decrypt",localStorage[ipcRenderer.sendSync("encrypt","username")]),
      password: ipcRenderer.sendSync("decrypt",localStorage[ipcRenderer.sendSync("encrypt","password")])
    },
    timeout: 100000,
    params: {
      //clientVersion: ipcRenderer.sendSync('getClientVersion'),
      //clientHash: ipcRenderer.sendSync('getClientHash'),
      //clientPlatform: process.platform,
    },
  };
  try {
    const response = await new_axios(axiosConfig, vue);
    const json = await response.data;
    const { data } = json;
    let jwt = data["x-access-token"];
    console.log(jwt);
    vue.$emit("reAuth", jwt);
  } catch (err) {
    console.log(err);
  }
}

function new_axios(axiosConfig_app, vue_app) {
  console.log('api.js')
  vue = vue_app;
  axiosConfig = axiosConfig_app;
  let method = axiosConfig.method.toLowerCase();
  let url = axiosConfig.url.replace(baseURL, "");
  let data,config = {};
  if(axiosConfig.timeout != null){
    config.timeout = axiosConfig.timeout;
  }
  data = axiosConfig.params;
  if (axiosConfig.headers != null &&
    axiosConfig.headers["x-access-token"] != null
  ) {
    instance.defaults.headers["x-access-token"] =
      axiosConfig.headers["x-access-token"];
  }
  instance.defaults.auth = axiosConfig.auth;

  switch (method) {
    case "post":
      return instance.post(url, data, config);
    case "get":
      return instance.get(url, { params: data , timeout : config.timeout}, config);
    case "delete":
      return instance.delete(url, { params: data }, config);
    case "put":
      return instance.put(url, data);
    case "patch":
      return instance.patch(url, data);
    default:
      console.log(`未知的 method: ${method}`);
      return false;
  }
}

export default new_axios;
