<script>
const $ = require("jquery");
const {shell} = require('electron');
//const axios = require("axios");
import axios from "../../axios/api";
import { Tooltip } from "bootstrap";
import baseURL from "../../axios/config";

export default {
  data() {
    return {
      chapterList: [],
      courseInfo: {},
      eMaterial: "",
      examInfo: [],
      chosedExamInfo: 0,
      n_item: {},
    };
  },
  methods: {
    changePage(page, tvMaterialId) {
      console.log(this.item);
      this.$emit("changePage", {
        page: page,
        tvMaterialId: tvMaterialId,
        item: {
          choose_subject: this.item.choose_subject,
          choose_material: tvMaterialId,
          courseInfo: this.courseInfo,
          chapterList: this.chapterList,
        },
      });
    },
    formatTime(time, format) {
      let second = Math.floor(time % 60);
      let min = Math.floor(time / 60) % 60;
      let hour = Math.floor(time / 3600);
      if (format != null) {
        return (
          " " +
          hour +
          format +
          (min < 10 ? "0" + min : min) +
          format +
          (second < 10 ? "0" + second : second)
        );
      } else {
        return " " + hour + " 小時 " + min + " 分鐘";
      }
    },
    choseExamInfo(index) {
      this.chosedExamInfo = index;
    },
    async checkDiligent() {
      const axiosConfig = {
        method: "get",
        url: baseURL+"/course/checkDiligent",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo:
            this.item.choose_subject.F_SUBJECT_NO || this.item.fSubjectNo,
          subjectNo: this.item.choose_subject.SUBJECT_NO || this.item.subjectNo,
          isApply: "N",
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
        if (json.rtnCode == "OK") {
          if (json.data[0].O_STATUS == "N") {
            this.$emit("callWarn", json.message);
          } else {
            this.applyDiligent();
          }
        } else {
          this.$emit("callWarn", "請聯繫客服");
        }
      } catch (err) {
        this.$emit("callWarn", "請聯繫客服");
        console.log(err);
      }
    },
    async applyDiligent() {
      const axiosConfig = {
        method: "get",
        url: baseURL+"/course/checkDiligent",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo:
            this.item.choose_subject.F_SUBJECT_NO || this.item.fSubjectNo,
          subjectNo: this.item.choose_subject.SUBJECT_NO || this.item.subjectNo,
          isApply: "Y",
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    openTkbtv(){
      shell.openExternal('https://www.tkbtv.com.tw')
    }
  },
  props: {
    jwt: {
      type: String,
      require: true,
    },
    item: {
      type: Object,
      require: true,
      default: function () {
        return { courseInfo: { USABLE_TIMES: 0, GIFT_TIMES: 0 } };
      },
    },
  },
  computed: {
    changeTime() {
      let time =
        this.item.choose_subject.TIMES != null
          ? this.item.choose_subject.TIMES
          : this.item.courseInfo.USABLE_TIMES + this.item.courseInfo.GIFT_TIMES;
      let min = Math.floor(time / 60) % 60;
      let hour = Math.floor(time / 3600);
      return " " + hour + " 小時 " + min + " 分鐘";
    },
    watchPercent() {
      let total = 0;
      let watch = 0;
      for (let i = 0; this.chapterList.length > i; i++) {
        total += this.chapterList[i].TIME;
        if (this.chapterList[i].FINAL_TOKEN != null) {
          watch += this.chapterList[i].FINAL_TOKEN;
        }
      }
      console.log(watch);
      console.log(total);
      console.log(Math.floor(watch / total) * 100);
      return Math.floor((watch * 100) / total);
    },
    last_days() {
      try {
        let now = new Date();
        let exam_date = new Date(
          this.examInfo[this.chosedExamInfo].EXAM_DATE_BEGIN
        );
        if (exam_date.getTime() - now.getTime() > 0) {
          return Math.floor(
            (exam_date.getTime() - now.getTime()) / (24 * 60 * 60 * 1000)
          );
        } else {
          return Math.floor(
            (new Date(
              now.getFullYear() +
                1 +
                "/" +
                this.examInfo[this.chosedExamInfo].EXAM_DATE_BEGIN
            ).getTime() -
              now.getTime()) /
              (24 * 60 * 60 * 1000)
          );
        }
      } catch (error) {
        return "";
      }
    },
  },
  watch: {
    chosedExamInfo: function (n, o) {
      console.log(n, o);
    },
    examInfo: function () {
      examFilter.init();
    },
  },
  async mounted() {
    document.getElementsByClassName("vue-loaders")[0].hidden = false;
    var tooltipTriggerList = [].slice.call(
      document.querySelectorAll('[data-bs-toggle="tooltip"]')
    );
    tooltipTriggerList.map(function (tooltipTriggerEl) {
      return new Tooltip(tooltipTriggerEl);
    });
    const examAxiosConfig = {
      method: "get",
      url: baseURL+"/course/getExamInfo",
      headers: { "x-access-token": this.jwt },
      params: {
        fSubjectNo: this.item.F_SUBJECT_NO,
        subjectNo: this.item.SUBJECT_NO,
        versionNo: this.item.VERSION_NO,
        id: this.item.ID,
      },
    };
    const examresponse = await axios(examAxiosConfig);
    const examjson = await examresponse.data;
    console.log(examjson);
    this.examInfo = examjson.data[0];
    this.chosedExamInfo = 0;

    console.log(this.item);
    const axiosConfig = {
      method: "get",
      url:baseURL+"/course/getMakeupChapter",
      headers: { "x-access-token": this.jwt },
      params: {
        fSubjectNo:
          this.item.choose_subject.F_SUBJECT_NO || this.item.fSubjectNo,
        subjectNo: this.item.choose_subject.SUBJECT_NO || this.item.subjectNo,
        versionNo: this.item.choose_subject.VERSION_NO || this.item.versionNo,
        id: this.item.choose_subject.ID || this.courseInfo.ID,
      },
    };
    try {
      const response = await axios(axiosConfig, this);
      const json = await response.data;
      console.log(json); 
      try{
          this.eMaterial = json.data.eMaterial;
          this.chapterList = json.data.chapterList;
          this.courseInfo = json.data.courseInfo;
          document.getElementsByClassName("vue-loaders")[0].hidden = true;
      }catch(e){
          alert(json.message);
          document.getElementsByClassName("vue-loaders")[0].hidden = false;
          this.changePage("course");
          document.getElementsByClassName("vue-loaders")[0].hidden = false;
      }
      /*
      switch (json.rtnCode) {
        case "success":
          this.eMaterial = json.data.eMaterial;
          this.chapterList = json.data.chapterList;
          this.courseInfo = json.data.courseInfo;
          document.getElementsByClassName("vue-loaders")[0].hidden = true;
          break;
        case "error":
          alert(json.message);
          document.getElementsByClassName("vue-loaders")[0].hidden = false;
          this.changePage("course");
          document.getElementsByClassName("vue-loaders")[0].hidden = false;
          break;
      }
      */
    } catch (err) {
      console.log(err);
    }
  },
};

const examFilter = {
  toggleList() {
    $("#examBTN").on("click", () => {
      $("#examList").stop().slideToggle(200);
      $("#examList").scrollTop(0);
      this.examItem();
      this.closeList();
    });
  },
  examItem() {
    $("#examList")
      .find("li")
      .on("click", function () {
        let selectItem = $(this);
        let target = selectItem.data("value");
        $("#examBTN").html(selectItem.text());
        this.chosedExamInfo = target;
        $("#examBTN").attr("data-exam", target);
        selectItem.addClass("active").siblings().removeClass("active");
        $("#examList").slideUp(200);
      });
  },
  closeList() {
    $("body").on("click", (e) => {
      if (e.target.id === "examBTN") return;
      $("#examList").slideUp(200);
    });
  },
  init() {
    this.toggleList();
  },
};
</script>

