<template>
  <header>
    <nav class="navbar navbar-expand bg-white g-0">
      <div class="container-fluid d-flex align-items-md-end">
        <div class="navbar-brand-logo d-flex">
          <a class="navbar-brand ms-lg-5">
            <img src="img/TKBtv-LOGO.svg" alt="TKBtv雲端函授" />
          </a>
        </div>
        <div class="emergent d-flex align-items-end justify-content-end">
          <!--
          <img
            src="img/emergency-icon.svg"
            alt="緊急通知"
            width="5%"
            class="pb-1 emergent-icon d-none d-md-block"
          />
          <span
            class="emergent-word text text-info border-bottom border-info text-nowrap me-3 d-none d-md-block"
            >&nbsp;&nbsp;緊急通知！緊急通知！這裡只能放二十個字。</span
          >-->
          <button
            class="btn btn-secondary btn-secondary-logout text-nowrap text"
            type="submit"
          >
            <a
              @click="back_click"
              class="btn-padding btn-back-padding text-white"
            >
              <template v-if="back_btn == 'back'">
                <i
                  class="fas fa-arrow-circle-left me-2 text-white"
                ></i></template
              >{{ back_btn }}
            </a>
          </button>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
export default {
  name:'header-area',
  data() {
    return {
      pre_page: "",
    };
  },
  props: {
    page: { type: String, require: true },
  },
  computed: {
    back_btn: function () {
      let text = "back";
      switch (this.page) {
        case "course-page":
        case "news-page":
        case "calendar-page":
          text = "登出";
          break;
        default:
          break;
      }
      return text;
    },
  },
  methods: {
    back_click() {
      console.log(this.page);
      switch (this.page) {
        case "chapter-page":
          this.$emit("changePage", {
            page: "course",
          });
          break;
        case "video-page":
          this.$emit("changePage", {
            page: this.pre_page.split('-')[0],
          });
          break;
        default:
          this.$emit("changePage", {
            page: "login",
          });
          break;
      }
    },
  },
  watch: {
    page: function (_new, _old) {
      this.pre_page = _old;
    },
  },
};
</script>

