<template>
  <main>
    <div class="videoplayer-wrap">
      <vue-loaders
        name="ball-scale-ripple-multiple"
        color="#176FA7"
        style="width: 100%;justify-content: center;height: 100%;display: flex;align-content: center;align-items: center;z-index: 99;background-color: #fff;"
      ></vue-loaders>
      <div class="row page-scroll thin-scrollbar align-items-start mx-auto">
        <div class="col-xl-8 videoplayer h-100">
          <video-player
            :videoUrl="videoUrl"
            id="tkbplayer"
            class="video-js vjs-default-skin vjs-16-9 vjs-big-play-centered"
          ></video-player>
          <div
            class="server-block text d-flex justify-content-between justify-content-xxl-around align-items-center p-3"
          >
            <div class="server-Select">
              <label for="serverSelect">伺服器</label>
              <select id="serverSelect" @change="changeServer($event)">
                <template v-for="n in serverCount" :key="n">
                  <option>{{ n }}</option>
                </template>
              </select>
              <!-- <i class="fas fa-user-tag text-white"></i>
                        <i class="fas fa-users text-white"></i> -->
            </div>
            <div class="watch">
              本次觀看時間 <span>{{ formatTime(watch_time, ":") }}</span>
            </div>
            <!-- <div class="greeting"><span class="name text-nowrap">OOO</span> <span class="text-nowrap">您好！</span></div> -->
            <!--
            <div class="hours-remain w-md-100">
              <span class="text-nowrap pe-1">課程時數</span
              ><span class="text-success text-nowrap"
                >尚餘 {{ formatTime(item.courseInfo.USABLE_TIMES) }}</span
              >
            </div>
            
            <button
              type="button"
              class="bg-success rounded-pill p-2 px-3"
              data-bs-toggle="modal"
              data-bs-target="#centumModal"
            >
              已完成
            </button>
            -->
          </div>
        </div>

        <!-- 頁籤 -->
        <div class="col-xl-4 video-list video-bar h-100 mt-4 mt-xl-0">
          <ul class="nav nav-tabs video-list-p title" id="myTab" role="tablist">
            <li class="nav-item flex-fill" role="presentation">
              <button
                class="nav-link active"
                id="home-tab"
                data-bs-toggle="tab"
                data-bs-target="#videoList"
                type="button"
                role="tab"
                aria-controls="home"
                aria-selected="true"
              >
                <img
                  src="img/icon-list.svg"
                  alt="影片列表"
                  style="width: 27px;"
                  class="me-1"
                />
                影片列表
              </button>
            </li>
            <li class="nav-item flex-fill" role="presentation">
              <button
                class="nav-link"
                id="profile-tab"
                data-bs-toggle="tab"
                data-bs-target="#download"
                type="button"
                role="tab"
                aria-controls="profile"
                aria-selected="false"
              >
                <img
                  src="img/icon-tag.svg"
                  alt=" 下載教材"
                  style="width: 21px;"
                />
                書籤列表
              </button>
            </li>
          </ul>
          <div class="tab-content  bg-light rounded-cos" id="myTabContent">
            <div
              class="tab-pane fade show active video-list-h"
              id="videoList"
              role="tabpanel"
              aria-labelledby="home-tab"
            >
              <ul class="video-scroll-h page-scroll scrollbar text pe-1">
                <template v-for="chapter in item.chapterList" :key="chapter.ID">
                  <li
                    :class="[
                      'video-list-item',
                      chapter.FINAL_TOKEN != null &&
                      chapter.FINAL_TOKEN > chapter.TIME * 0.9
                        ? 'video-list-fill'
                        : '',
                    ]"
                    :style="{backgroundImage: 
                    
                    'linear-gradient( 90deg, #cbebfb 0% ' + (chapter.FINAL_TOKEN / chapter.TIME) * 100 + '%,#EDF8FF' + (chapter.FINAL_TOKEN / chapter.TIME) * 100 +'%)',
                      
                      backgroundColor: chapter.ID == selected_chapter.ID ? '#FFF6A4':''
                    }"
                    @click="selectChapter(chapter)"
                  >
                    {{ chapter.NAME
                    }}<span class="video-list-time ms-3">{{
                      formatTime(chapter.TIME, ":")
                    }}</span>
                  </li>
                </template>
              </ul>
            </div>
            <div
              class="tab-pane fade video-list-h position-relative p-3"
              id="download"
              role="tabpanel"
              aria-labelledby="profile-tab"
            >
              <ul
                class="video-scroll-h tag-scroll-h  page-scroll scrollbar text pe-3"
              >
                <!-- <li class="non-tag text-center d-none"><button type="button" data-bs-toggle="modal" data-bs-target="#tagModal">記錄重要的瞬間<img src="img/icon-plus.svg" alt="新增標籤" style="width: 24px;" class="ms-2"></button></li> -->
                <template
                  v-for="bookmark in bookmark_list"
                  v-bind:key="bookmark.id"
                >
                  <li class="tag-style">
                    <span class="d-flex align-items-center"
                      ><img
                        :src="
                          'https://www.tkbgo.com.tw/tvPlayer/upload/student/' +
                            bookmark.img_url
                        "
                        alt="螢幕截圖"
                        class="me-3 screenshots-w"
                      />{{ bookmark.memo }}<br />{{
                        formatTime(bookmark.token, ":")
                      }}</span
                    ><span
                      class="tag-del text-primary"
                      style="font-size: 60px;"
                      @click="removeBookmark(bookmark.id)"
                      >&times;</span
                    >
                  </li>
                </template>
              </ul>
              <div class="add-tag position-absolute title text-nowrap">
                <button
                  type="button"
                  data-bs-toggle="modal"
                  data-bs-target="#tagModal"
                  @click="captureVideo()"
                >
                  <span>記錄重要的瞬間</span>
                  <img
                    src="img/icon-plus.svg"
                    alt="新增標籤"
                    style="width: 35px;"
                    class="ms-2 mb-1"
                  />
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- tagModal -->
      <div
        class="modal fade"
        id="tagModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content rounded-3 bg-light">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">
                <button
                  type="button"
                  class="btn text-primary title"
                  data-bs-dismiss="modal"
                >
                  <i class="fas fa-arrow-circle-left me-2 text-primary"></i>back
                </button>
                <canvas
                  id="capture_img"
                  src="img/fake-screenshot.jpg"
                  alt="影片截圖"
                  class="w-100"
                >
                </canvas>
                <input type="text" hidden id="capture_img_token" />
              </h5>
            </div>
            <div class="modal-body">
              <div class="answer text-white text">
                <textarea
                  id="qacontent"
                  class="textarea w-100 rounded-3"
                  spellcheck="false"
                  required="true"
                  placeholder="請輸入書籤內容(限50字)"
                  type="text"
                  maxlength="50"
                ></textarea>
              </div>
            </div>
            <div class="modal-footer justify-content-center">
              <!-- <button type="button" class="btn text-white sm-text" data-bs-dismiss="modal">Close</button> -->
              <button
                type="submit"
                class="btn bg-primary text-white btn-send py-2 px-5 rounded-pill mt-3 title "
                data-bs-dismiss="modal"
                @click="addBookmark"
              >
                送出
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- centumModal -->
      <div
        class="modal fade"
        id="centumModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content rounded-3 bg-success">
            <div class="modal-body">
              <div class="answer text-primary text mt-3 px-3">
                目前觀看 <span>50%</span><br />
                確定將本章節觀看進度調為100% ?
              </div>
              <div class="modal-footer justify-content-between">
                <button
                  type="submit"
                  class="btn bg-primary text-white btn-send py-2 px-5 rounded-pill mt-3 title "
                  data-bs-dismiss="modal"
                  @click="finish_chapter"
                >
                  確定
                </button>
                <button
                  type="submit"
                  class="btn bg-info text-white btn-send py-2 px-5 rounded-pill mt-3 title "
                  data-bs-dismiss="modal"
                >
                  取消
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import videojs from "video.js";
import axios from "axios";
import videoPlayer from "./videoPlayer.vue";
var player = null;

export default {
  data() {
    return {
      courseInfo: {},
      eMaterial: "",
      examInfo: [],
      chosedExamInfo: 0,
      chapterList: [],
      serverCount: 10,
      watchTimerTime: 300,
      watch_time: 0,
      videoUrl: "",
      timer_start: true,
      timer: null,
      choose_material: 0,
      bookmark_list: [],
      selected_chapter: {},
    };
  },
  async mounted() {
    console.log(this.item);
    await this.getInitConfig();
    this.chapterList = this.item.chapterList;
    this.choose_material = this.item.choose_material;
    this.selected_chapter = this.chapterList.filter((chapter) => {
      if (chapter.ID == this.choose_material) {
        return true;
      }
    })[0];
    //console.log('selected_chapter'+this.selected_chapter)
    this.iniplayer();
    //await init();
    /*
    await this.changeVideo(null, this.choose_material);
    await this.getBookmark();
    */
    document.getElementsByClassName("vue-loaders")[0].hidden = true;
  },
  props: {
    jwt: {
      type: String,
      require: true,
    },
    item: {
      type: Object,
      require: true,
      default: function() {
        return {};
      },
    },
  },
  components: {
    videoPlayer,
  },
  methods: {
    /**
     * 取得播放器初始化支設定
     */
    async getInitConfig() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/config/getInitConfig",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        const { serverCount, watchTimer } = json;
        console.log(serverCount);
        this.serverCount = serverCount;
        this.watchTimerTime = watchTimer;
      } catch (err) {
        console.log(err + "🇹🇼");
      }
    },
    iniplayer() {
      player = videojs("tkbplayer", {
        /*
        sources: [
          {
            src:
              "https://media.tkbtv.com.tw/vod2/_definst_/mp4:TKB02/2019/144V1303EM0000001/144-01-01-02.mp4/playlist.m3u8?wowzatokenendtime=1631806091&wowzatokenhash=SGhBB7lkDvEL9gNI3wecjokqYK3OHvVQ35XPQjgqrJQ=",
            type: "application/x-mpegURL",
          },
        ],
        */
        controlBar: {
          volumePanel: {
            inline: true,
          },
        },
        hls: true,
        vhs: true,
        dash: true,
        loop: true,
        muted: false,
        width: "600",
        height: "400px",
        controls: true,
        playbackRates: [3, 2, 1.75, 1.5, 1.25, 1, 0.5],
      });
      player.ready(() => {
        player.on("play", this.player_play_handler);

        player.on("playing", () => {
          console.log("playing");
          this.timer_start = true;
        });

        player.on("waiting", () => {
          console.log("waiting");
          this.timer_start = false;
        });
        player.on("pause", () => {
          console.log("播放暫停");
          this.timer_start = false;
        });

        player.on("ended", () => {
          console.log("影片結束");
          this.timer_start = false;
          this.player.stop();
        });

        player.on("error", () => {
          this.timer_start = false;
        });
      });
    },
    // 播放章節
    async changeVideo(chapter, server) {
      server = server != null ? server : 1;
      this.choose_material = chapter != null ? chapter.ID : this.choose_material;
      //player.pause();
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getVideoUrl",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
          server: server,
        },
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        this.videoUrl = json.data.videoUrl;
        const { videoUrl } = json.data;
        console.log(videoUrl);
        player.src({ src: videoUrl, type: "application/x-mpegURL" });
        player.load();
        if (this.selected_chapter != null && this.selected_chapter.TOKEN > 0) {
          if (confirm("是否從上次觀看位置開始?")) {
            player.currentTime(this.selected_chapter.TOKEN);
            player.play();
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
    // 完成章節
    async finish_chapter() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/finishChapter",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
        },
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    player_play_handler() {
      if (!this.timer) {
        this.timer = setInterval(() => {
          if (this.timer_start) {
            console.log(`計時器` + this.watch_time);
            this.watch_time += 1;
          }
        }, 1000);
      }
    },
    formatTime(time, format) {
      let second = Math.floor(time % 60);
      let min = Math.floor(time / 60) % 60;
      let hour = Math.floor(time / 3600);
      if (format != null) {
        return (
          " " +
          hour +
          format +
          (min < 10 ? "0" + min : min) +
          format +
          (second < 10 ? "0" + second : second)
        );
      } else {
        return " " + hour + " 小時 " + min + " 分鐘";
      }
    },
    // 取得書籤
    async getBookmark() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/bookmark/getBookmark",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
        },
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        this.bookmark_list = json.data;
      } catch (err) {
        console.log(err);
      }
    },
    // 移除書籤
    async removeBookmark(bookmarkId) {
      const axiosConfig = {
        method: "POST",
        url: "https://www.tkbgo.com.tw/api/bookmark/removeBookmark",
        headers: { "x-access-token": this.jwt },
        params: {
          bookmarkId,
        },
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        console.log(json);
        this.getBookmark();
      } catch (err) {
        console.log(err);
      }
    },
    // 跳出書籤窗框並截圖
    async captureVideo() {
      let capture_canvas = document.getElementById("capture_img");
      capture_canvas.height = player.videoHeight();
      capture_canvas.width = player.videoWidth();
      let capture_ctx = capture_canvas.getContext("2d");
      capture_ctx.drawImage(document.getElementsByTagName("video")[0], 0, 0);
      document.getElementById("capture_img_token").value = player.currentTime();
    },
    // 將書籤資訊上傳
    async addBookmark() {
      let capture_canvas = document.getElementById("capture_img");
      let dataURL = capture_canvas.toDataURL("image/jpeg");
      let blobBin = atob(dataURL.split(",")[1]);
      let array = [];
      for (let i = 0; i < blobBin.length; i++) {
        array.push(blobBin.charCodeAt(i));
      }
      let file = new Blob([new Uint8Array(array)], { type: "image/jpeg" });

      let formData = new FormData();
      formData.set(
        "token",
        Math.floor(document.getElementById("capture_img_token").value)
      );
      formData.set("memo", document.getElementById("qacontent").value);
      formData.set("fSubjectNo", this.item.choose_subject.F_SUBJECT_NO);
      formData.set("subjectNo", this.item.choose_subject.SUBJECT_NO);
      formData.set("versionNo", this.item.choose_subject.VERSION_NO);
      formData.set("tvMaterialId", this.choose_material);
      formData.append("file", file, new Date().getTime() + ".jpeg");
      const axiosConfig = {
        method: "post",
        url: "https://www.tkbgo.com.tw/api/bookmark/uploadImg",
        headers: { "x-access-token": this.jwt },
        data: formData,
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        console.log(json);
        this.getBookmark();
      } catch (err) {
        console.log(err);
      }
    },
    selectChapter(chapter) {
      this.selected_chapter = chapter;
    },
    async watchVideoLog() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/watchTime",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          id: this.item.choose_subject.ID,
        },
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    changeServer(server) {
      this.changeVideo(null, server.target.value);
    },
    async chapterLogHandler() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/savePreviousTime",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
          previousTime: Math.floor(player.currentTime()),
        },
      };
      try {
        const response = await axios(axiosConfig,this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
  },
  watch: {
    selected_chapter: function() {
      this.changeVideo(this.selected_chapter, null);
      this.getBookmark();
    },
  },
  beforeUnmount() {
    clearInterval(this.timer);
    if (player) {
      player.dispose();
    }
  },
};
</script>

<style></style>
