<template>
  <div id="login-tab-content" class="active">
    <form class="signup-form" action="" method="post">
      <input
        type="email"
        class="input rounded-pill sm-text"
        id="user_email"
        autocomplete="off"
        placeholder="請輸入帳號"
      />
      <input
        type="password"
        class="input rounded-pill sm-text"
        id="user_password"
        autocomplete="off"
        placeholder="請輸入密碼"
      />
      <div class="form-check form-switch m-3">
        <label class="form-check-label sm-text" for="flexSwitchCheckDefault"
          >記住帳號密碼</label
        >
        <input
          class="form-check-input"
          type="checkbox"
          id="flexSwitchCheckDefault"
        />
      </div>
      <div class="button-login my-4" @click="login">
        <input
          type="button"
          class="button loginBTN rounded-pill text"
          id="login"
        />
        <span class="login-text">登入</span>
        <span class="button-c"></span>
      </div>
      <div class="help-text d-flex justify-content-between sm-text px-2">
        <p>
          <a
            class="text-decoration-none"
            href="https://www.tkbtv.com.tw/front/login/toLogin.action"
            target="_blank"
            >忘記密碼？</a
          >
        </p>

        <p>
          <a
            class="text-decoration-none"
            href="https://www.tkbtv.com.tw/front/register/toRegister.action"
            target="_blank"
            >註冊會員</a
          >
        </p>
      </div>
      <div class="g-signin2" data-onsuccess="onSignIn"></div>
      <!--.help-text-->
    </form>
    <!--.login-form-->
  </div>
</template>
<script>
const $ = require("jquery");
import "googleapis";
import axios from "../../axios/api";
import { ipcRenderer } from "electron";
export default {
  data() {
    return {
      // app_version: 'v'+ipcRenderer.sendSync("getVersion"),
    };
  },
  methods: {
    async login() {
      let username = document.getElementById("user_email").value;
      let password = document.getElementById("user_password").value;
      if (document.getElementById("flexSwitchCheckDefault").checked) {
        localStorage.setItem(
          ipcRenderer.sendSync("encrypt", "username"),
          ipcRenderer.sendSync("encrypt", username)
        );
        localStorage.setItem(
          ipcRenderer.sendSync("encrypt", "password"),
          ipcRenderer.sendSync("encrypt", password)
        );
        localStorage.setItem(
          "is_rembmer",
          document.getElementById("flexSwitchCheckDefault").checked
        );
      }
      await this.auth(username, password);
    },
    async auth(username, password) {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/member/auth",
        headers: {
          // remove headers
        },
        auth: {
          username,
          password,
        },
        timeout: 100000,
        params: {
          //clientVersion: ipcRenderer.sendSync('getClientVersion'),
          //clientHash: ipcRenderer.sendSync('getClientHash'),
          //clientPlatform: process.platform,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { data } = json;
        let jwt = data["x-access-token"];
        console.log(jwt);
        this.$emit("changePage", { jwt: jwt, page: "Frame" });
      } catch (err) {
        this.$emit("callWarn", "帳號密碼錯誤");
      }
      $(".button-login").removeClass("active");
    },
  },
  mounted() {
    if (localStorage[ipcRenderer.sendSync("encrypt", "username")] != null) {
      document.getElementById("user_email").value = ipcRenderer.sendSync(
        "decrypt",
        localStorage[ipcRenderer.sendSync("encrypt", "username")]
      );
      document.getElementById("user_password").value = ipcRenderer.sendSync(
        "decrypt",
        localStorage[ipcRenderer.sendSync("encrypt", "password")]
      );
      document.getElementById("flexSwitchCheckDefault").checked =
        localStorage.is_rembmer;
    }
    //登入按鈕延遲.7s & 帳號密碼錯誤
    const loginFunc = {
      access: false,
      trigger() {
        $(".btn-close").on("click", () => {
          this.closeAlert();
        });
        $(".button-login").on("click", (e) => {
          $(e.currentTarget).addClass("active");
        });
      },

      closeAlert() {
        $(".alert-login").removeClass("show");
      },

      init() {
        this.trigger();
        $("#login-tab-content")
          .find("input")
          .on("keyup", (e) => {
            if (e.keyCode === 13) $("#login").click();
          });
      },
    };
    loginFunc.init();
  },
};
</script>
