<template>
  <canvas class="waterflow"></canvas>
</template>

<script>
//import p5 from "p5";
import $ from "jquery";
export default {
  name: 'waterflow-area',
  props: {
    show_personinfo: Boolean,
    memberInfo: Object,
  },
  data() {
    return {
      canvas: {},
      ctx: {},
      vx: 0,
      vy: 0,
      x: 0,
      y: 0,
      count: 0,
      max_count: Math.floor(Math.random()*120)+60
    };
  },
  mounted() {
    this.canvas = $("canvas")[0];
    document.getElementById("tkbplayer").prepend(this.canvas);
    console.log(document.getElementById("tkbplayer").offsetWidth);
    console.log(document.getElementById("tkbplayer").offsetHeight);
    this.canvas.width = document.getElementById("tkbplayer").offsetWidth;
    this.canvas.height = document.getElementById("tkbplayer").offsetHeight;
    this.vx = Math.random();
    this.vy = Math.random();
    this.ctx = this.canvas.getContext("2d");
    //setInterval(this.time_count, 1000);
    setInterval(this.draw1, 1000);
  },
  methods: {
    time_count() {
      this.count += 1;
    },
    draw() {
      this.count += 1;
      let ctx = this.ctx;
      ctx.clearRect(0, 0, this.canvas.width, this.canvas.height); // clear canvas
      if (this.count < 60) {
        ctx.fillStyle = "rgb(0,0,0,0.35)";
        ctx.fillText(
          this.memberInfo.studentName +
            //this.count +
            "-" +
            /*
            this.memberInfo.idNo +
            "-" +
            */
            this.memberInfo.phone,
          this.x,
          this.y
        );
      } else if (this.count > 120) {
        this.count = 0;
      }
      //ctx.fillText("123", this.x, this.y);

      if (this.x > this.canvas.width - 60 || this.x < 0) {
        this.vx = this.vx * -1;
      }
      if (this.y > this.canvas.height - 10 || this.y < 0) {
        this.vy = this.vy * -1;
      }
      this.x += this.vx;
      this.y += this.vy;
      window.requestAnimationFrame(this.draw);
    },
    /**
     * 消失隨機出現浮水印
     */
    draw1() {
      let ctx = this.ctx;
      ctx.clearRect(0, 0, this.canvas.width, this.canvas.height); // clear canvas
      if (this.count == 0){
        this.x = Math.floor(Math.random() * this.canvas.width);
        this.y = Math.floor(Math.random() * this.canvas.height);
      }
      this.count += 1;
      if (this.count < this.max_count) {
        ctx.fillStyle = "rgb(0,0,0,0.3)";
        ctx.fillText(
          this.memberInfo.studentName +
            //this.count +
            "-" +
            /*
            this.memberInfo.idNo +
            "-" +
            */
            this.memberInfo.phone,
          this.x,
          this.y
        );
      } else if (this.count > this.max_count*2) {
        this.count = 0;
        this.max_count = Math.floor(Math.random()*120)+60;
      }
    },
    /**
     * 四角跑馬燈
     */
    draw2() {
      this.count += 1;
      let ctx = this.ctx;
      ctx.clearRect(0, 0, this.canvas.width, this.canvas.height); // clear canvas
      if (this.y == 0 || (this.canvas.width -100 > this.x && this.y == 30)) {
        this.y = 30;
        this.x += 1;
      } else if (this.canvas.width -100 == this.x && this.canvas.height-10 > this.y) {
        this.y += 1;
      } else if (
        this.canvas.width -100 >= this.x &&
        this.canvas.height -10 == this.y && this.x > 0
      ) {
        this.x -= 1;
      } else {
        this.y -= 1;
      }
      ctx.fillStyle = "rgb(200,200,200,0.6)";
      ctx.fillText(
        this.memberInfo.studentName +
          //this.count +
          "-" +
          /*
            this.memberInfo.idNo +
            "-" +
            */
          this.memberInfo.phone,
        this.x,
        this.y
      );
      window.requestAnimationFrame(this.draw2);
    },
  },
};
</script>

<style>
.waterflow {
  position: absolute;
  top: 0;
  pointer-events: none;
  z-index: 100;
  width: 100%;
  height: 100%;
}
</style>
