<template>
  <component
    :is="page"
    @changePage="changePage"
    @reAuth="reAuth"
    @callWarn="callWarn"
    :system="system"
    :cjwt="jwt"
  ></component>
  <div
    :class="[
      'alert',
      'alert-login',
      'alert-info',
      'alert-dismissible',
      'fade',
      'd-flex',
      'align-items-center',
      'title',
    ]"
    role="alert"
  >
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      fill="currentColor"
      class="bi bi-exclamation-triangle-fill flex-shrink-0 me-2"
      viewBox="0 0 16 16"
      role="img"
      aria-label="Warning:"
    >
      <path
        d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767L8.982 1.566zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5zm.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"
      />
    </svg>
    <strong>{{ warn_msg }}</strong>
    <button type="button" class="btn-close" aria-label="Close"></button>
  </div>
  <div
    class="toast-container position-absolute bottom-0 end-0 p-3"
    id="toastPlacement"
  ></div>
</template>

<script>
import Login from "./components/login/LoginPage.vue";
import Frame from "./components/public/frame.vue";
import FrameM from "./components/public/frameM.vue";
const $ = require("jquery");
export default {
  name: "App",
  components: {
    Login,
    Frame,
    FrameM,
  },
  data() {
    return {
      page: "login",
      jwt: "",
      warn_msg: "",
    };
  },
  methods: {
    changePage(data) {
      this.jwt = data.jwt;
      this.page = data.page;
      if (data.system != null) {
        this.system = data.system;
      }
    },
    reAuth(jwt) {
      console.log("jwt" + jwt);
      this.jwt = jwt;
    },
    /**
     * 呼叫跳窗
     */
    callWarn(data) {
      console.log("callWarn", data);
      this.warn_msg = data;
      $(".alert").addClass("show");
      $(".alert").addClass(data.class);
      setTimeout(function () {
        $(".alert").removeClass("show");
      }, 5000);
    },
  },
  mounted() {},
};
</script>

