<script>
//const axios = require("axios");
import axios from "../../axios/api";
export default {
  data() {
    return {
      c_jwt: this.jwt,
      tList: [],
      fList: [],
      sList: [],
      memberInfo: {},
    };
  },
  methods: {
    changePage(item) {
      console.log(item);
      this.$emit("changePage", {
        page: "chapter",
        item: {
          choose_subject: item,
        },
      });
    },
    //取得課程
    async getCourse() {
      document.getElementsByClassName("vue-loaders")[0].hidden = false;
      console.log(document.getElementsByClassName("vue-loaders")[0].hidden);
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getMyCourse",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { data } = json;
        this.tList = data.tList;
        this.fList = data.fList;
        this.sList = data.sList;
      } catch (err) {
        console.log(err);
      }
    },
    //取得會員資料
    async getMemberinfo() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/member/getMemberInfo",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { data } = json;
        console.log(data);
        this.memberInfo = data;
      } catch (err) {
        console.log(err);
      }
    },
  },
  watch: {
    jwt: async function () {
      this.c_jwt = this.jwt;
      console.log("jwt變化");
      document.getElementsByClassName("vue-loaders")[0].hidden = false;
      await this.getMemberinfo();
      await this.getCourse();
      document.getElementsByClassName("vue-loaders")[0].hidden = true;
    },
  },
  props: {
    jwt: {
      type: String,
      require: true,
    },
  },
  async mounted() {
    document.getElementsByClassName("vue-loaders")[0].hidden = false;
    await this.getMemberinfo();
    await this.getCourse();
    document.getElementsByClassName("vue-loaders")[0].hidden = true;
  },
  beforeUnmount() {
    document.getElementsByClassName("vue-loaders")[0].hidden = false;
    console.log("beforeUnmount");
  },
};
</script>

