<template>
  <sidebar class="sidebar">
    <div
      class="sidebar-height d-flex justify-content-center align-items-center"
    >
      <div
        class="nav fixed-bottom-sm flex-lg-column justify-content-around pt-3"
        id="v-pills-tab"
        role="tablist"
        aria-orientation="vertical"
      >
        <button
          type="button"
          class="nav-link text"
          id="v-pills-calendar-tab"
          data-bs-toggle="pill"
          data-bs-target="#v-pills-calendar"
          role="tab"
          aria-controls="v-pills-home"
          aria-selected="true"
          @click="changePage('calendar')"
        >
          <div class="ac-calendar d-none d-lg-block"></div>
          <div class="ac-sm-w ac-calendar-sm d-block d-lg-none"></div>
          讀書計畫
        </button>
        <button
          type="button"
          class="nav-link text"
          id="v-pills-home-tab"
          data-bs-toggle="pill"
          data-bs-target="#v-pills-home"
          role="tab"
          aria-controls="v-pills-home"
          aria-selected="true"
          @click="changePage('course')"
        >
          <div class="ac-home d-none d-lg-block"></div>
          <div class="ac-sm-w ac-home-sm d-block d-lg-none"></div>
          我的課程
        </button>
        <button
          @click="changePage('news')"
          type="button"
          class="nav-link text"
          id="v-pills-news-tab"
          data-bs-toggle="pill"
          data-bs-target="#v-pills-news"
          role="tab"
          aria-controls="v-pills-news"
          aria-selected="false"
        >
          <div class="ac-news d-none d-lg-block"></div>
          <div class="ac-sm-w ac-news-sm d-block d-lg-none"></div>
          最新消息
        </button>
        <button
          type="button"
          @click="changePage('notice')"
          class="nav-link text"
          id="v-pills-warn-tab"
          data-bs-toggle="pill"
          data-bs-target="#v-pills-warn"
          role="tab"
          aria-controls="v-pills-warn"
          aria-selected="false"
        >
          <div class="ac-warn d-none d-lg-block"></div>
          <div class="ac-sm-w ac-warn-sm d-block d-lg-none"></div>
          通知列表
        </button>
        <button
          type="button"
          class="nav-link text"
          @click="changePage('service')"
          id="v-pills-service-tab"
          data-bs-toggle="pill"
          data-bs-target="#v-pills-service"
          role="tab"
          aria-controls="v-pills-service"
          aria-selected="false"
        >
          <div class="ac-service d-none d-lg-block"></div>
          <div class="ac-sm-w ac-service-sm d-block d-lg-none"></div>
          客服列表
        </button>
      </div>
    </div>
  </sidebar>
</template>

<script>
export default {
  name:'side-area',
  props: {
    page: { type: String, require: true },
  },
  methods: {
    changePage(page) {
      this.$emit("changePage", {
        page: page,
      });
    },
  },
  watch: {
    page: function () {
      console.log("side_watch:" + this.page);
      let buttons = document.getElementsByTagName("button");
      for (let i = 0; buttons.length > i; i++) {
        buttons[i].classList.remove("active");
      }
      switch (this.page) {
        case "course-page":
        case "chapter-page":
        case "video-page":
          document.getElementById("v-pills-home-tab").classList.add("active");
          break;
        case "calendar-page":
          document
            .getElementById("v-pills-calendar-tab")
            .classList.add("active");
          break;
        case "news-page":
          document.getElementById("v-pills-news-tab").classList.add("active");
          break;
        case "notice-page":
          document.getElementById("v-pills-warn-tab").classList.add("active");
          break;
        case "service-page":
          document
            .getElementById("v-pills-service-tab")
            .classList.add("active");
          break;
      }
    },
  },
  mounted(){
      switch (this.page) {
        case "course-page":
        case "chapter-page":
        case "video-page":
          document.getElementById("v-pills-home-tab").classList.add("active");
          break;
        case "calendar-page":
          document
            .getElementById("v-pills-calendar-tab")
            .classList.add("active");
          break;
        case "news-page":
          document.getElementById("v-pills-news-tab").classList.add("active");
          break;
        case "notice-page":
          document.getElementById("v-pills-warn-tab").classList.add("active");
          break;
        case "service-page":
          document
            .getElementById("v-pills-service-tab")
            .classList.add("active");
          break;
      }  }
};
</script>

