<template>
  <div id="signup-tab-content" class="active">
    <form class="login-form d-flex" action="" method="post">
      <p class="text fw-bold mb-0">快速登入</p>
      <img id="loginQRcode" src="img/QuickLogin.png" alt="QR Code" />
      <div class="tabs overflow-visible">
        <a
          class="active text text-decoration-none"
          href="#scan-tab-content"
          @click="toTakoDownload"
          >沒有TAKO? 點我下載
          <i class="fas fa-arrow-alt-circle-down text mt-1 ms-3"></i
        ></a>
      </div>
    </form>
    <!--.login-form-->
  </div>
</template>
<style></style>
<script>
const QRCode = require("qrcode");
//const axios = require("axios");
import axios from "../../axios/api";
import baseURL from "../../axios/config";
export default {
  data() {
    return {
      area: "login-area",
    };
  },
  methods: {
    toTakoDownload() {
      //this.$emit("changePage" ,{ jwt: 'eyJraWQiOiJrMSIsImFsZyI6IlJTMjU2In0.eyJpc3MiOiJ0a2J0di5jb20iLCJleHAiOjE2MzU3NTI5MDUsImp0aSI6ImdWU1FaSGppWXVyMGtVdmFCTDBqTlEiLCJpYXQiOjE2MzU3MzQ5MDUsIm5iZiI6MTYzNTczNDc4NSwiaWQiOiJUVzAxOTEwMDYzNDQifQ.EOBOGCPzw02e7OPGEd1Xf1qYtOV-5PUOGLHsa1zofPJbMvQFYj3ZG10RvH8icH3XvmbDWOrfIQckuwssi9Eg5FTXg4hof29QZu3zqcI33pg75nnuOFFnJRCeDutkcSKsdUoRMWg2s67laTinyhtQ1h4OsQ5w3seibvEvI44nBkbV3U1ZI9Pg4CvGVxfj-KaQ-aWfL9_EZdrRSot6jAVm0veoxFL_0FBvrm67e4mKSWjtfbj11zSxNpVqntIt84Scjj8X--kKYqRWwSxKWMZSYQaXgCS-Ch-RwdPJ7WlcvnzcgEncK-EwH5hopUHzTR5PbCP-b9XF_zq1XupQFtnuBA', page: "FrameM" });
      this.$emit("toTakoDownload");
    },
    /**
     * QR code 初始區
     */
    async genQRcode() {
      //產生隨機16碼金鑰用於當作對應個人的編號
      now_key = this.getRandomKey(16);
      console.log(now_key);
      //key = 'Kx5EMfhkfsjACrAr';
      this.getQRCode(now_key);
      this.getQRConnect(now_key);
    },
    /**
     * 生成亂碼
     * @param {*} length
     */
    getRandomKey(length) {
      let key =
        "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
      let randomKey = "";
      for (let i = 0; length > i; i++) {
        let num = Math.floor(Math.random() * key.length);
        randomKey += key[num];
      }
      return randomKey;
    },
    /**
     * 取得QRcode
     * @param {*} key
     */
    async getQRCode(key) {
      const axiosConfig = {
        method: "get",
        url: baseURL+"/member/getQRcodeInfo",
        params: {
          key: key,
        },
      };
      const response = await axios(axiosConfig, this);
      const json = await response.data;
      const { message } = json;
      QRCode.toDataURL(
        message,
        { errorCorrectionLevel: "H", width: 200 },
        function(err, canvas) {
          if (err) throw err;
          document.getElementById("loginQRcode").src = canvas;
        }
      );
    },
    /**
     * 與QRcodeServer建立長連線,並在掃描後回傳登入資訊並登入
     * @param {*} key
     */
    async getQRConnect(key) {
      console.log("建立QRcode連線");
      const axiosConfig = {
        method: "get",
        url: baseURL+"/member/getCode",
        timeout: 1800000,
        params: {
          key: key,
        },
      };
      const response = await axios(axiosConfig, this);
      const json = await response.data;
      const { message, status } = json;
      switch (status) {
        case "logout":
          break;
        case "success":
          alert("登入成功");
          now_key = "";
          this.authMakeup(message.id_no, message.student_no);
          break;
        case "message":
          this.alert(message);
          break;
        case "timeout":
          this.genQRcode();
          break;
      }
    },
    /**
     * server驗證帳密
     * @param {string} id_no - 身分證
     * @param {string} student_no - 學號
     */
    async authMakeup(id_no, student_no) {
      const axiosConfig = {
        method: "get",
        url: baseURL+"/member/authMakeup",
        auth: {
          username: id_no,
          password: student_no,
        },
        timeout: 15000,
      };
      try {
        const response = await axios(axiosConfig);
        const json = await response.data;
        const { data } = json;
        let jwt = data["x-access-token"];
        if (jwt != null) {
          // 驗證成功
          console.log(jwt);
          //this.$emit("changePage", { jwt: jwt, page: "Frame" });
          this.$emit("changePage", { jwt: jwt, page: "FrameM" });
          //getMainPageUrl('makeup');
          //checkDuplicate();
          // 開始偵測錄影程式
          //ipcRenderer.send('checkIllegalApp');
        } else {
          alert("登入失敗");
        }
      } catch (err) {
        if (err.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx
          const errorData = err.response.data.error;
          alert(errorData.message);
        } else if (err.request) {
          // The request was made but no response was received
          alert("無法與伺服器建立連線");
        } else {
          // Something happened in setting up the request that triggered an Error
          alert("Error", err.message);
        }
      }
    },
  },
  mounted() {
    this.genQRcode();
  },
};
var now_key;
</script>
