<script>
import $ from "jquery";
//const axios = require("axios");
import "jquery-datetimepicker";
import axios from "../../axios/api";
import "@fullcalendar/core/vdom"; // solves problem with Vite
import FullCalendar from "@fullcalendar/vue3";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin, { Draggable } from "@fullcalendar/interaction";
import { Modal, Carousel } from "bootstrap";
const weekName = ["日", "一", "二", "三", "四", "五", "六"];
$.datetimepicker.setLocale("zh-TW");
export default {
  data() {
    return {
      calendarOptions: {
        fixedMirrorParent: document.body,
        plugins: [dayGridPlugin, timeGridPlugin, interactionPlugin],
        initialView: "dayGridMonth",
        locale: "zh-tw",
        navLinks: false, // 可以點選天/周的名稱來瀏覽檢視嗎
        events: [],
        headerToolbar: {
          center: "prevYear,prev,title,next,nextYear",
          left: "dayGridMonth,timeGridWeek",
          right: "today",
        },
        moreLinkContent: function (arg) {
          return "+" + arg.num + "顯示更多";
        },
        buttonText: {
          today: "今天",
          month: "月",
          week: "周",
          day: "天",
        },
        aspectRatio: 2,
        variable: true,
        editable: true, //是否可以修改日曆事件
        droppable: true, // this allows things to be dropped onto the calendar
        drop: this.dropHandler,
        eventDrop: this.eventDrop,
        eventReceive: this.eventReceive,
        eventClick: this.eventClick,
        dayHeaderContent: function (arg) {
          return weekName[arg.date.getDay()];
        },
        dayCellContent: function (arg) {
          return arg.date.getDate();
        },
        views: {
          timeGrid: {
            dayMaxEventRows: 9, // adjust to 28 only for timeGridWeek/timeGridDay
          },
        },
        columnFormat: [
          {
            month: "ddd", // Mon
            week: "ddd M/d", // Mon 9/7
            day: "dddd M/d", // Monday 9/7
          },
        ],
        //月曆顯示更多：高度
        moreLinkClick: this.moreLinkClick,

        eventDurationEditable: false, //允許通過調整大小來更改事件的持續時間
        // moreLinkClick: "week",  //點選more按鈕時候，跳到什麼檢視
        dayMaxEventRows: true, // for all non-TimeGrid views
      },
      courses: [],
      tList: [],
      fList: [],
      sList: [],
      selected_course: { material_list: [], scheduledChapters: [] },
      selected_chapter: {},
      tutorial_page: 0,
    };
  },
  components: {
    FullCalendar, // make the <FullCalendar> tag available
  },
  props: {
    jwt: {
      type: String,
      require: true,
    },
  },
  methods: {
    moreLinkClick() {
      let calendarApi = this.$refs.calendar.getApi();
      setTimeout(() => {
        if (calendarApi.currentData.currentViewType === "dayGridMonth") {
          $(".fc-popover-body").css({
            "max-height": "116px",
            transition: ".5s",
          });
        }
      }, 50);
    },
    /**
     * 事件點擊
     */
    eventClick(info) {
      // alert(info.event.title)
      // let breakWord1 = info.event.title.indexOf("\n");
      // let breakWord2 = info.event.title.lastIndexOf("\n");
      // let newInfoTitle = info.event.title.substring(breakWord1, breakWord2);
      let newInfoTitle = info.event.title.replaceAll("\n", " ");
      var myModal = new Modal(document.getElementById("editModal"), {
        keyboard: false,
      });
      myModal.show();
      /**
       * 將選取的課程置換到選取的參數
       */
      //this.selected_course.ID = info.event.extendedProps.TSM_ID;
      this.selected_course.F_SUBJECT_NO = info.event.extendedProps.F_SUBJECT_NO;
      this.selected_course.SUBJECT_NO = info.event.extendedProps.SUBJECT_NO;
      this.selected_course.VERSION_NO = info.event.extendedProps.VERSION_NO;
      this.selected_chapter = info.event.extendedProps;
      console.log(this.selected_chapter);
      $("#watchCourseButton").on("click", () => {
        this.watchCourse(myModal);
      });
      $('button[data-bs-target="#editModal"]').one(
        "click",
        () => {
          this.deleteEvent(newInfoTitle, info, myModal);
        }
      );
      console.log($('button[data-bs-target="#editModal"]').onclick);
    },
    /**
     * 刪除課程
     */
    async deleteEvent(newInfoTitle, info, myModal) {
      console.log("刪除課程");
      console.log(newInfoTitle, info, myModal);
      await this.delCalendarDetail(info);
      /**
       * 判斷刪除的章節是否為選中的課程
       * 是才塞回並排序
       */
      console.log(this.selected_course.ID, info.event.extendedProps.TSM_ID);

      if (this.selected_course.ID == info.event.extendedProps.TSM_ID) {
        this.selected_course.scheduledChapters.forEach((chapter, index) => {
          if (newInfoTitle.indexOf(chapter.D_NAME) >= 0) {
            this.selected_course.material_list.push(chapter);
            this.selected_course.scheduledChapters.splice(index, 1);
          }
        });
        this.selected_course.material_list =
          this.selected_course.material_list.sort(); //排序
        this.selected_course.material_list =
          this.selected_course.material_list.sort((a, b) => a.SORT - b.SORT); //排序
      }
      info.event.remove();
      myModal.hide();
    },
    /**
     * 刪除全任務
     */
    async deleteAllEvent() {
      console.log("刪除任務");
      if (confirm("確認刪除嗎")) {
        let calendarApi = this.$refs.calendar.getApi();
        let ca_events = calendarApi.getEvents();
        console.log(this.calendarOptions.events);
        for (let i = 0; ca_events.length > i; i++) {
          let o = ca_events[i].extendedProps;
          let event_start_date = ca_events[i].start;
          if (this.selected_course.ID == o.TSM_ID) {
            console.log("比對" + this.selected_course.ID + "vs" + o.TSM_ID);
            console.log(event_start_date);
            let new_event = JSON.parse(JSON.stringify(o));
            new_event.ID = new_event.MATERIAL_ID;
            this.selected_course.material_list.push(new_event);
            /*
            this.selected_course.scheduledChapters.forEach((chapter, index) => {
              if (chapter.MATERIAL_ID == ) {
                let new_event = JSON.parse(JSON.stringify(o));
                new_event.ID = new_event.MATERIAL_ID;
                this.selected_course.material_list.push(new_event);
                this.selected_course.scheduledChapters.splice(index, 1);
              }
            });
            */
            this.selected_course.material_list =
              this.selected_course.material_list.sort(
                (a, b) => a.SORT - b.SORT
              ); //排序
            ca_events[i].remove();
          }
        }
        const axiosConfig = {
          method: "get",
          url: "https://www.tkbgo.com.tw/api/calendar/delCalendarByTsmId",
          headers: { "x-access-token": this.jwt },
          params: {
            tsm_id: this.selected_course.ID,
            end_date: $("#del_timepicker_end").val(),
            start_date: $("#del_timepicker_start").val(),
          },
        };
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
        await this.getMyCalendar();
      }
    },
    /**
     * 點選觀看課程
     */
    async watchCourse(myModal) {
      console.log(this.selected_course);
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getCourseDetail",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.selected_chapter.F_SUBJECT_NO,
          subjectNo: this.selected_chapter.SUBJECT_NO,
          versionNo: this.selected_chapter.VERSION_NO,
          id: this.selected_chapter.TSM_ID,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        //const response = await axios_new('get','course/getCourseDetail',axiosConfig,this)
        const json = await response.data;
        //console.log(json);
        switch (json.rtnCode) {
          case "success":
            this.chapterList = json.data.chapterList;
            this.courseInfo = json.data.courseInfo;
            myModal.hide();
            this.$emit("changePage", {
              page: "video",
              tvMaterialId: this.selected_chapter.MATERIAL_ID,
              item: {
                choose_subject: {
                  F_SUBJECT_NO: this.selected_chapter.F_SUBJECT_NO,
                  SUBJECT_NO: this.selected_chapter.SUBJECT_NO,
                  VERSION_NO: this.selected_chapter.VERSION_NO,
                  ID: this.selected_chapter.TSM_ID,
                },
                choose_material: this.selected_chapter.MATERIAL_ID,
                chapterList: this.chapterList,
                courseInfo: this.courseInfo,
              },
            });
            break;
          case "error":
            myModal.hide();
            this.$emit("callWarn", json.message);
            break;
        }
      } catch (err) {
        console.log(err);
      }
    },
    eventReceive(info) {
      console.log("eventReceive");
      console.log("事件數:" + this.calendarOptions.events.length);
      console.log(info);
      this.addCalendarDetail(info);
    },
    dropHandler(info) {
      console.log("dropHandler");
      console.log("事件數:" + this.calendarOptions.events.length);
      console.log("事件數:" + this.$refs.calendar.getApi().getEvents().length);
      console.log(info);
      let selectedItem = this.selected_course.material_list.filter(
        (chapter) => chapter.MATERIAL_ID == info.draggedEl.dataset.material_id
      );
      this.selected_course.scheduledChapters.push(selectedItem[0]);
      console.log(this.selected_course.scheduledChapters);
      this.selected_course.material_list =
        this.selected_course.material_list.filter(
          (chapter) => chapter.MATERIAL_ID != info.draggedEl.dataset.material_id
        );
    },
    //取得課程
    async getCourse() {
      console.log(this.jwt);
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getMyCourse",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { data } = json;
        console.log(data);
        this.courses = data.tList.concat(data.fList).concat(data.sList);
        this.tList = data.tList;
        this.fList = data.fList;
        this.sList = data.sList;
      } catch (err) {
        if (err.response.status == 401) {
          console.log("認證失敗");
        }
        console.log(err.response.status);
      }
    },
    /**
     * 列表選擇課程並展開
     */
    async select_course(course) {
      this.selected_course.F_SUBJECT_NO = course.F_SUBJECT_NO;
      this.selected_course.SUBJECT_NO = course.SUBJECT_NO;
      this.selected_course.ID = course.ID;
      this.selected_course.NAME =
        course.NAME != null ? course.NAME : course.CHINESE_NAME;
      this.selected_course.VERSION_NO = course.VERSION_NO;
      this.selected_course.TIMES = course.TIMES;
      this.selected_course.scheduled_time = 0;
      this.selected_course.course_time = 0;
      //取得選擇課程的章節列表
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/calendar/getCalendarChapter",
        headers: { "x-access-token": this.jwt },
        params: {
          version_no: this.selected_course.VERSION_NO,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        await this.getCourseDetail();
        this.selected_course.material_list = json.filter((material) => {
          let same = true;
          if (material.SET_TYPE == 1) {
            this.selected_course.scheduled_time += material.TIME;
          }
          this.selected_course.course_time += material.TIME;
          this.selected_course.scheduledChapters.forEach((scheduledChapter) => {
            if (scheduledChapter.MATERIAL_ID == material.MATERIAL_ID) {
              same = false;
              return;
            }
          });
          return same;
        });
        console.log(this.selected_course.scheduledChapters);
        console.log(json);
      } catch (err) {
        console.log(err);
      }
      console.log(this.selected_course);
    },
    /**
     * 取得課程資訊
     */
    async getCourseDetail() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getCourseDetail",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.selected_course.F_SUBJECT_NO,
          subjectNo: this.selected_course.SUBJECT_NO,
          versionNo: this.selected_course.VERSION_NO,
          id: this.selected_course.ID,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        switch (json.rtnCode) {
          case "success":
            console.log(json.data);
            this.eMaterial = json.data.eMaterial;
            this.chapterList = json.data.chapterList;
            this.courseInfo = json.data.courseInfo;
            break;
          case "error":
            this.$emit("callWarn", json.message);
            //alert(json.message);
            //window.open("https://www.tkbtv.com.tw");
            break;
        }
      } catch (err) {
        console.log(err);
      }
    },
    /**
     * 取得月曆已經排的章節
     */
    async getMyCalendar() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/calendar/getCalendarList",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log("getMyCalendar資料:");
        console.log(json);
        // 資料處理將時間置入FullCalender判斷的欄位中
        json.data.forEach((data) => {
          data.start = data.STUDY_DATE;
          data.title =
            data.SUBJECT_NAME +
            "\n" +
            data.D_NAME +
            "\n" +
            "進度：" +
            (data.FINAL_TOKEN != null
              ? Math.floor(
                  (parseInt(data.FINAL_TOKEN) / parseInt(data.TIME)) * 100
                )
              : 0) +
            "%";
        });
        // console.log("加至行事曆的資料" + json.data);
        this.calendarOptions.events = json.data;
        console.log("事件數:" + this.calendarOptions.events.length);
        let calendarApi = this.$refs.calendar.getApi();
        let ca_events = calendarApi.getEvents();
        console.log("日曆內部事件數:" + ca_events.length);
        this.selected_course.scheduledChapters =
          this.selected_course.scheduledChapters.concat(json.data);
      } catch (err) {
        console.log(err);
      }
    },
    async delCalendarDetail(info) {
      console.log(info);
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/calendar/delCalendarDetail",
        headers: { "x-access-token": this.jwt },
        params: {
          calendar_id: info.event.extendedProps.ID,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    async addCalendarDetail(info) {
      console.log(info);
      let study_date =
        info.event.start.getFullYear() +
        "-" +
        [
          "01",
          "02",
          "03",
          "04",
          "05",
          "06",
          "07",
          "08",
          "09",
          "10",
          "11",
          "12",
        ][info.event.start.getMonth()] +
        "-" +
        (info.event.start.getDate() < 10
          ? "0" + info.event.start.getDate()
          : info.event.start.getDate());
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/calendar/setCalendarDetail",
        headers: { "x-access-token": this.jwt },
        params: {
          s_date: study_date,
          tsm_id: this.selected_course.ID,
          f_subject_no: this.selected_course.F_SUBJECT_NO,
          subject_no: this.selected_course.SUBJECT_NO,
          material_id: info.event.extendedProps.material_id,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        info.event.setExtendedProp("STUDY_DATE", study_date);
        info.event.setExtendedProp(
          "F_SUBJECT_NO",
          this.selected_course.F_SUBJECT_NO
        );
        info.event.setExtendedProp(
          "SUBJECT_NO",
          this.selected_course.SUBJECT_NO
        );
        info.event.setExtendedProp(
          "VERSION_NO",
          this.selected_course.VERSION_NO
        );
        info.event.setExtendedProp("TSM_ID", this.selected_course.ID);
        info.event.setExtendedProp(
          "MATERIAL_ID",
          info.event.extendedProps.material_id
        );
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    async addCalendarByAuto() {
      let chose_week = [];
      let week_day_checks = $('[name="week_day"]');
      for (let i = 0; week_day_checks.length > i; i++) {
        if (week_day_checks[i].checked) {
          console.log(week_day_checks[i].value + "被選取");
          chose_week.push(week_day_checks[i].value);
        }
      }
      let f_subject_no = this.selected_course.F_SUBJECT_NO;
      let subject_no = this.selected_course.SUBJECT_NO;
      let version_no = this.selected_course.VERSION_NO;
      let tsm_id = this.selected_course.ID;
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/calendar/addCalendarByAuto",
        headers: { "x-access-token": this.jwt },
        params: {
          week_day: chose_week.toString(),
          start_date: $("#add_timepicker_start").val(),
          end_date: $("#add_timepicker_end").val(),
          f_subject_no,
          subject_no,
          version_no,
          tsm_id,
          num: document
            .getElementById("numList")
            .getElementsByClassName("active")[0].dataset.value,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json.data);
        for (let k = 0; json.data.length > k; k++) {
          this.selected_course.material_list =
            this.selected_course.material_list.filter((o) => {
              if (o.ID == json.data[k].MATERIAL_ID) {
                return false;
              } else {
                return true;
              }
            });
        }
        await this.getMyCalendar();
        let calendarApi = this.$refs.calendar.getApi();
        let ca_events = calendarApi.getEvents();
        console.log("事件數:" + ca_events.length);
      } catch (err) {
        console.log(err);
      }
    },
    async eventDrop(info) {
      console.log("eventDrop");
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/calendar/delCalendarDetail",
        headers: { "x-access-token": this.jwt },
        params: {
          calendar_id: info.oldEvent._def.extendedProps.ID,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
      let study_date =
        info.event.start.getFullYear() +
        "-" +
        [
          "01",
          "02",
          "03",
          "04",
          "05",
          "06",
          "07",
          "08",
          "09",
          "10",
          "11",
          "12",
        ][info.event.start.getMonth()] +
        "-" +
        (info.event.start.getDate() < 10
          ? "0" + info.event.start.getDate()
          : info.event.start.getDate());
      const add_axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/calendar/setCalendarDetail",
        headers: { "x-access-token": this.jwt },
        params: {
          s_date: study_date,
          tsm_id: info.oldEvent._def.extendedProps.TSM_ID,
          f_subject_no: info.oldEvent._def.extendedProps.F_SUBJECT_NO,
          subject_no: info.oldEvent._def.extendedProps.SUBJECT_NO,
          material_id: info.oldEvent._def.extendedProps.MATERIAL_ID,
        },
      };
      try {
        const response = await axios(add_axiosConfig, this);
        const json = await response.data;
        console.log(json);
        info.event.setExtendedProp("ID", json.data);
      } catch (err) {
        console.log(err);
      }
      console.log(info);
    },
    formatTime(time, format) {
      let second = Math.floor(time % 60);
      let min = Math.floor(time / 60) % 60;
      let hour = Math.floor(time / 3600);
      if (format != null) {
        return (
          " " +
          hour +
          format +
          (min < 10 ? "0" + min : min) +
          format +
          (second < 10 ? "0" + second : second)
        );
      } else {
        return " " + hour + " 小時 " + min + " 分鐘";
      }
    },
    tutorial_next() {
      this.tutorial_page++;
      if (this.tutorial_page > 3) {
        $("#carouselGuidedIndicators").fadeOut(400, function () {
          setTimeout(() => {
            $("#carouselGuidedIndicators").hide();
          });
        });
        localStorage.setItem("watch_tutorial", true);
        return false;
      }
    },
    tutorial_start() {
      $("#carouselGuidedIndicators").show();
      this.tutorial_page = 0;
    },
  },
  unmounted() {
    $(".xdsoft_").remove();
  },
  /**
   * Vue 初始化
   */
  async mounted() {
    document.getElementsByClassName("vue-loaders")[0].hidden = false;
    $("#add_timepicker_start").datetimepicker({
      format: "Y-m-d",
      onShow: function () {
        this.setOptions({
          maxDate: $("#add_timepicker_end").val()
            ? $("#add_timepicker_end").val()
            : false,
        });
      },
      timepicker: false,
    });
    $("#add_timepicker_end").datetimepicker({
      format: "Y-m-d",
      onShow: function () {
        this.setOptions({
          minDate: $("#add_timepicker_end").val()
            ? $("#add_timepicker_start").val()
            : false,
        });
      },
      timepicker: false,
    });
    $("#del_timepicker_start").datetimepicker({
      format: "Y-m-d",
      onShow: function () {
        this.setOptions({
          maxDate: $("#add_timepicker_end").val()
            ? $("#add_timepicker_end").val()
            : false,
        });
      },
      timepicker: false,
    });
    $("#del_timepicker_end").datetimepicker({
      format: "Y-m-d",
      onShow: function () {
        this.setOptions({
          minDate: $("#add_timepicker_end").val()
            ? $("#add_timepicker_start").val()
            : false,
        });
      },
      timepicker: false,
    });

    /**
     * 教學初始化
     */
    var myCarousel = document.querySelector("#carouselGuidedIndicators");
    new Carousel(myCarousel, {
      interval: false,
      touch: false,
    });
    $(".carousel-control-next").click(this.tutorial_next);

    if (localStorage.watch_tutorial == null) {
      $("#carouselGuidedIndicators").show();
    } else {
      $("#carouselGuidedIndicators").hide();
    }

    //Guided顯示點擊
    $("#openHelpBTN").click(this.tutorial_start);

    $("#closeHelpBTN").click(function () {
      $("#carouselGuidedIndicators").hide();
    });

    //Guided Carousel END
    await this.getCourse();
    await this.getMyCalendar();
    let calendarApi = this.$refs.calendar.getApi();
    let ca_events = calendarApi.getEvents();
    console.log("日曆內部試件數:" + ca_events.length);
    let containerEl = document.getElementById("external-events");
    new Draggable(containerEl, {
      itemSelector: ".fc-event",
      eventData: function (eventEl) {
        // let data = $('.fc-event-course').text() + eventEl.innerText + '進度：20%';
        return {
          title:
            $(".fc-event-course").text() +
            "\n" +
            eventEl.innerText +
            "\n" +
            "進度：" +
            (eventEl.dataset.final_token != null
              ? Math.floor(
                  (parseInt(eventEl.dataset.final_token) /
                    parseInt(eventEl.dataset.time)) *
                    100
                )
              : 0) +
            "%",
          resourceEditable: false,
          material_id: eventEl.dataset.material_id,
          D_NAME: eventEl.dataset.d_name,
          SUBJECT_NAME: eventEl.dataset.subject_name,
          SORT: eventEl.dataset.sort,
        };
      },
    });
    const numFilter = {
      toggleList() {
        $("#numBTN").on("click", () => {
          $("#numList").stop().slideToggle(200);
          $("#examList").scrollTop(0);
          this.numItem();
          this.closeList();
        });
      },
      numItem() {
        $("#numList")
          .find("li")
          .on("click", function () {
            let selectItem = $(this);
            let target = selectItem.data("value");
            $("#numBTN").html(selectItem.text());
            $("#numBTN").attr("data-exam", target);
            selectItem.addClass("active").siblings().removeClass("active");
            $("#numList").slideUp(200);
          });
      },
      closeList() {
        $("body").on("click", (e) => {
          if (e.target.id === "numBTN") return;
          $("#numList").slideUp(200);
        });
      },
      init() {
        this.toggleList();
      },
    };
    numFilter.init();
    document.getElementById("menu-3").checked = false;
    document.getElementsByClassName("vue-loaders")[0].hidden = true;
  },
  beforeUnmount() {
    document.getElementById("menu-3").checked = false;
  },
  watch: {
    selected_course: function () {
      document.getElementById("menu-3").checked = false;
    },
  },
};
</script>
