<template>
  <main>
    <div class="chapter bg-success rounded-3 overflow-hidden">
      <vue-loaders
        name="ball-scale-ripple-multiple"
        color="#176FA7"
        scal="4.0"
        style="
          width: 100%;
          justify-content: center;
          height: 100%;
          display: flex;
          align-content: center;
          align-items: center;
          z-index: 99;
          background-color: #fff;
        "
      ></vue-loaders>
      <div class="hello title fw-bold text-primary mt-4">
        {{ memberInfo.studentName }}您好，歡迎回來！
      </div>
      <div class="row page-scroll L-scrollbar justify-content-center">
        <div class="col-lg-5 col-10 my-0 mb-lg-4 pb-5 gx-0">
          <div
            class="
              page-header
              bg-primary
              text-white
              rounded-pill
              title
              lh-lg
              text-center
            "
          >
            銜補課程清單
          </div>
          <div class="chapter-course bg-white rounded-3 my-3">
            <div
              class="
                chapter-course-space
                d-flex
                justify-content-between
                align-items-start
              "
              v-for="item in extended_list"
              :key="item.F_SUBJECT_NO"
              v-cloak
            >
              <a draggable="false" @click="changePage(item)">
                <div class="mx-md-5 mx-3 title">
                  {{ item.CHINESE_NAME }}
                </div>
              </a>
            </div>
          </div>
        </div>
        <div class="col-lg-5 col-10 my-0 mb-lg-4 pb-5 gx-0">
          <div
            class="
              page-header
              bg-primary
              text-white
              rounded-pill
              title
              lh-lg
              text-center
            "
          >
            補課課程清單
          </div>
          <div class="chapter-course bg-white rounded-3 my-3">
            <div
              class="
                chapter-course-space
                d-flex
                justify-content-between
                align-items-start
              "
              v-for="item in supplement_list"
              :key="item.F_SUBJECT_NO"
              v-cloak
            >
              <a draggable="false" @click="changePage(item)">
                <div class="mx-md-5 mx-3 title">
                  {{ item.CHINESE_NAME }}
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
//const axios = require("axios");
import axios from "../../axios/api";
import baseURL from "../../axios/config";
export default {
  data() {
    return {
      c_jwt: this.jwt,
      supplement_list: [],
      extended_list: [],
      memberInfo: {},
    };
  },
  methods: {
    changePage(item) {
      console.log(item);
      this.$emit("changePage", {
        page: "chapter",
        item: {
          choose_subject: item,
        },
      });
    },
    //取得課程
    async getCourse() {
      document.getElementsByClassName("vue-loaders")[0].hidden = false;
      console.log(document.getElementsByClassName("vue-loaders")[0].hidden);
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getMakeupCourse",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { data } = json;
        this.supplement_list = data.supplement_list;
        this.extended_list = data.extended_list;
      } catch (err) {
        console.log(err);
      }
    },
    //取得會員資料
    async getMemberinfo() {
      const axiosConfig = {
        method: "get",
        url: baseURL + "/member/getMemberInfo",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { data } = json;
        console.log(data);
        this.memberInfo = data;
      } catch (err) {
        console.log(err);
      }
    },
  },
  watch: {
    jwt: async function () {
      this.c_jwt = this.jwt;
      console.log("jwt變化");
      document.getElementsByClassName("vue-loaders")[0].hidden = false;
      // await this.getMemberinfo();
      await this.getCourse();
      document.getElementsByClassName("vue-loaders")[0].hidden = true;
    },
  },
  props: {
    jwt: {
      type: String,
      require: true,
    },
  },
  async mounted() {
    document.getElementsByClassName("vue-loaders")[0].hidden = false;
    //await this.getMemberinfo();
    await this.getCourse();
    document.getElementsByClassName("vue-loaders")[0].hidden = true;
  },
  beforeUnmount() {
    document.getElementsByClassName("vue-loaders")[0].hidden = false;
    console.log("beforeUnmount");
  },
};
</script>

