<script>
import videojs from "video.js";
import axios from "axios";
import videoPlayer from "./videoPlayer.vue";
import waterFlow from "./waterflow.vue";
import 'videojs-hotkeys';

var player = null;

export default {
  data() {
    return {
      courseInfo: {},
      eMaterial: "",
      examInfo: [],
      chosedExamInfo: 0,
      chapterList: [],
      serverCount: 10,
      watchTimerTime: 300,
      watch_time: 0,
      videoUrl: "",
      timer_start: true,
      timer: null,
      choose_material: 0,
      bookmark_list: [],
      selected_chapter: {},
      memberInfo: {},
    };
  },
  async mounted() {
    console.log(this.item);
    if(this.item.courseInfo.USABLE_TIMES + this.item.courseInfo.GIFT_TIMES <= 0 ){
      this.$emit(
                "callWarn",
                `此課程已無時數，請至官網購買！`
              );
      this.changePage("chapter");
    }
    await this.getInitConfig();
    await this.getMemberinfo();
    this.chapterList = this.item.chapterList;
    this.choose_material = this.item.choose_material;
    this.selected_chapter = this.chapterList.filter((chapter) => {
      if (chapter.ID == this.choose_material) {
        return true;
      }
    })[0];
    /**
     * 版本伺服器
     *
    let normal_server = [7, 8, 3, 5, 3, 5];
    let sp_server = [2, 6];
    let sp_server2 = [11, 12];
    let sp_version = [
      "3892",
      "3920",
      "4014",
      "3169",
      "3822",
      "3861",
      "3752",
      "3896",
      "4031",
      "4074",
      "3911",
      "4030",
      "4075",
      "3897",
      "3032",
      "4068",
    ];
    //var sp_version1=['2568','2882','3589','3482','2955','2525','3773','3201','3189'];
    let sp_version2 = [
      "3198",
      "2593",
      "3916",
      "3615",
      "3809",
      "4063",
      "4035",
      "4005",
    ];
    let final_server = normal_server;
    //var ss = normal_server[Math.floor(Math.random() * normal_server.length)];
    try {
      for (let s = 0; s < sp_version.length; s++) {
        // 符合第一種 4 版本
        if (this.item.choose_subject.VERSION_NO == sp_version[s]) {
          //ss = sp_server[Math.floor(Math.random() * sp_server.length)];
          final_server = sp_server;
          break;
        }
      }
      for (let s = 0; s < sp_version2.length; s++) {
        if (this.item.choose_subject.VERSION_NO == sp_version2[s]) {
          //ss = sp_server2[Math.floor(Math.random() * sp_server2.length)];
          final_server = sp_server2;
          break;
        }
      }
    } catch (e) {
      console.log(e);
    }
    this.serverCount = final_server;
    */
    this.iniplayer();
    document.getElementsByClassName("vue-loaders")[0].hidden = true;
  },
  props: {
    jwt: {
      type: String,
      require: true,
    },
    item: {
      type: Object,
      require: true,
      default: function () {
        return {};
      },
    },
  },
  components: {
    videoPlayer,
    waterFlow,
  },
  methods: {
    /**
     * 取得播放器初始化支設定
     */
    async getInitConfig() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/config/getInitConfig",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { serverCount, watchTimer } = json;
        console.log(serverCount);
        await this.getVersionServer();
        console.log(serverCount);
        this.watchTimerTime = watchTimer;
      } catch (err) {
        console.log(err + "🇹🇼");
      }
    },
    changePage(page, tvMaterialId) {
      console.log(this.item);
      this.$emit("changePage", {
        page: page,
        tvMaterialId: tvMaterialId,
        item: {
          choose_subject: this.item.choose_subject,
          choose_material: tvMaterialId,
          courseInfo: this.courseInfo,
          chapterList: this.chapterList,
        },
      });
    },
    iniplayer() {
      player = videojs("tkbplayer", {
        /*
        sources: [
          {
            src:
              "https://media.tkbtv.com.tw/vod2/_definst_/mp4:TKB02/2019/144V1303EM0000001/144-01-01-02.mp4/playlist.m3u8?wowzatokenendtime=1631806091&wowzatokenhash=SGhBB7lkDvEL9gNI3wecjokqYK3OHvVQ35XPQjgqrJQ=",
            type: "application/x-mpegURL",
          },
        ],
        */
        controlBar: {
          volumePanel: {
            inline: true,
          },
        },
        hls: true,
        vhs: true,
        dash: true,
        loop: true,
        width: "600",
        height: "400px",
        controls: true,
        playbackRates: [3, 2, 1.75, 1.5, 1.25, 1, 0.5],
      });
      player.ready(() => {
        player.on("play", this.player_play_handler);

        player.hotkeys({
          volumeStep: 0.1,
          seekStep: 5,
          alwaysCaptureHotkeys: true,
        });

        player.on("playing", () => {
          console.log("playing");
          this.timer_start = true;
        });

        player.on("waiting", () => {
          console.log("waiting");
          this.timer_start = false;
        });
        player.on("pause", () => {
          console.log("播放暫停");
          this.timer_start = false;
        });

        player.on("ended", () => {
          console.log("影片結束");
          this.timer_start = false;
          this.player.stop();
        });

        player.on("error", () => {
          this.timer_start = false;
        });
      });
    },
    // 播放章節
    async changeVideo(chapter, server) {
      server = server != null ? server : this.serverCount[Math.floor(Math.random()*this.serverCount.length)];
      this.choose_material =
        chapter != null ? chapter.ID : this.choose_material;
      //player.pause();
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getVideoUrl",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
          server: server,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        this.videoUrl = json.data.videoUrl;
        const { videoUrl } = json.data;
        console.log(videoUrl);
        player.src({ src: videoUrl, type: "application/x-mpegURL" });
        player.load();
        if (this.selected_chapter != null && this.selected_chapter.TOKEN > 0) {
          if (confirm("是否從上次觀看位置開始?")) {
            player.currentTime(this.selected_chapter.TOKEN);
            player.play();
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
    bookmark_click(time) {
      player.currentTime(time);
    },
    // 完成章節
    async finish_chapter() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/finishChapter",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
        },
      };
      try {
        this.chapterList[
          this.chapterList.findIndex((o) => {
            return o.ID == this.selected_chapter.ID;
          })
        ].FINAL_TOKEN =
          this.chapterList[
            this.chapterList.findIndex((o) => {
              return o.ID == this.selected_chapter.ID;
            })
          ].TIME;
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    player_play_handler() {
      if (!this.timer) {
        this.timer = setInterval(() => {
          if (this.timer_start) {
            console.log(`計時器` + this.watch_time);
            if (
              this.watch_time > 0 &&
              this.watch_time % this.watchTimerTime == 0
            ) {
              console.log("扣時數");
              this.watchVideoLog();
              this.chapterLogHandler();
            }
            if (
              this.watch_time > 0 &&
              this.watch_time %
                (Math.floor(Math.random() * 30) * 60 + 30 * 60) ==
                0
            ) {
              player.pause();
              this.$emit(
                "callWarn",
                `同學 您已經看了一段時間了，為了您的眼睛著想，休息一下吧！`
              );
            }
            this.watch_time += 1;
          }
        }, 1000);
      }
    },
    formatTime(time, format) {
      let second = Math.floor(time % 60);
      let min = Math.floor(time / 60) % 60;
      let hour = Math.floor(time / 3600);
      if (format != null) {
        return (
          " " +
          hour +
          format +
          (min < 10 ? "0" + min : min) +
          format +
          (second < 10 ? "0" + second : second)
        );
      } else {
        return " " + hour + " 小時 " + min + " 分鐘";
      }
    },
    // 取得書籤
    async getBookmark() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/bookmark/getBookmark",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        this.bookmark_list = json.data;
      } catch (err) {
        console.log(err);
      }
    },
    // 移除書籤
    async removeBookmark(bookmarkId) {
      const axiosConfig = {
        method: "POST",
        url: "https://www.tkbgo.com.tw/api/bookmark/removeBookmark",
        headers: { "x-access-token": this.jwt },
        params: {
          bookmarkId,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
        this.getBookmark();
      } catch (err) {
        console.log(err);
      }
    },
    // 跳出書籤窗框並從影片中截圖
    async captureVideo() {
      let capture_canvas = document.getElementById("capture_img");
      capture_canvas.height = player.videoHeight();
      capture_canvas.width = player.videoWidth();
      let capture_ctx = capture_canvas.getContext("2d");
      capture_ctx.drawImage(document.getElementsByTagName("video")[0], 0, 0);
      document.getElementById("capture_img_token").value = player.currentTime();
    },
    // 將書籤資訊上傳
    async addBookmark() {
      let capture_canvas = document.getElementById("capture_img");
      let dataURL = capture_canvas.toDataURL("image/jpeg");
      let blobBin = atob(dataURL.split(",")[1]);
      let array = [];
      for (let i = 0; i < blobBin.length; i++) {
        array.push(blobBin.charCodeAt(i));
      }
      let file = new Blob([new Uint8Array(array)], { type: "image/jpeg" });
      let formData = new FormData();
      formData.set(
        "token",
        Math.floor(document.getElementById("capture_img_token").value)
      );
      formData.set("memo", document.getElementById("qacontent").value);
      formData.set("fSubjectNo", this.item.choose_subject.F_SUBJECT_NO);
      formData.set("subjectNo", this.item.choose_subject.SUBJECT_NO);
      formData.set("versionNo", this.item.choose_subject.VERSION_NO);
      formData.set("tvMaterialId", this.choose_material);
      formData.append("file", file, new Date().getTime() + ".jpeg");
      const axiosConfig = {
        method: "post",
        url: "https://www.tkbgo.com.tw/api/bookmark/uploadImg",
        headers: { "x-access-token": this.jwt },
        data: formData,
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        document.getElementById("qacontent").value = "";
        console.log(json);
        this.getBookmark();
      } catch (err) {
        console.log(err);
      }
    },
    selectChapter(chapter) {
      this.selected_chapter = chapter;
    },
    async watchVideoLog() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/watchTime",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          id: this.item.choose_subject.ID,
          tvMaterialId: this.choose_material,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    changeServer(server) {
      this.changeVideo(null, server.target.value);
    },
    async getVersionServer(){
        const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/getVersionServer",
        headers: { "x-access-token": this.jwt },
        params: {
          versionNo: this.item.choose_subject.VERSION_NO,
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
        this.serverCount = json.data;
      } catch (err) {
        console.log(err);
      }
    },
    async chapterLogHandler() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/course/savePreviousTime",
        headers: { "x-access-token": this.jwt },
        params: {
          fSubjectNo: this.item.choose_subject.F_SUBJECT_NO,
          subjectNo: this.item.choose_subject.SUBJECT_NO,
          versionNo: this.item.choose_subject.VERSION_NO,
          tvMaterialId: this.choose_material,
          previousTime: Math.floor(player.currentTime()),
        },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        console.log(json);
      } catch (err) {
        console.log(err);
      }
    },
    //取得會員資料
    async getMemberinfo() {
      const axiosConfig = {
        method: "get",
        url: "https://www.tkbgo.com.tw/api/member/getMemberInfo",
        headers: { "x-access-token": this.jwt },
      };
      try {
        const response = await axios(axiosConfig, this);
        const json = await response.data;
        const { data } = json;
        console.log(data);
        this.memberInfo = data;
      } catch (err) {
        console.log(err);
      }
    },
  },
  watch: {
    selected_chapter: function () {
      player.pause();
      this.changeVideo(this.selected_chapter, null);
      this.getBookmark();
    },
  },
  beforeUnmount() {
    clearInterval(this.timer);
    if (player) {
      player.dispose();
    }
  },
};
</script>

