<template>
  <div class="bg-white container-3xl">
    <head-block :page="page" @changePage="changePage" @reAuth="reAuth"></head-block>
    <side-block v-show="show_side" @changePage="changePage" @reAuth="reAuth"></side-block>
    <component
      :is="page"
      :jwt="cjwt"
      :item="item"
      @changePage="changePage"
      @reAuth="reAuth"
    ></component>
  </div>
</template>

<script>
import sideBlock from "./side.vue";
import headBlock from "./header.vue";
import coursePage from "../course/courseMPage.vue";
import chapterPage from "../course/chapterMPage.vue";
import videoPage from "../course/videoMPage.vue";
import newsPage from "../news/newsPage.vue";
import noticePage from "../notice/noticePage.vue";
import servicePage from "../service/servicePage.vue";
import calendarPage from "../calendar/calendarPage.vue";
export default {
  data() {
    return {
      page: "course-page",
      show_side: true,
      item: {},
    };
  },
  components: {
    headBlock,
    coursePage,
    sideBlock,
    chapterPage,
    videoPage,
    newsPage,
    noticePage,
    servicePage,
    calendarPage,
  },
  props: {
    cjwt: { type: String, require: true },
  },
  methods: {
    changePage(data) {
      console.log(data.item);
      this.item = data.item || this.item;
      if (data.page == "login") {
        this.$emit("changePage", {
          page: "login",
        });
      } else {
        this.page = data.page + "-page";
        if (data.page === "video") {
          this.show_side = false;
        } else {
          this.show_side = true;
        }
      }
    },
    reAuth(jwt) {
      this.$emit('reAuth',jwt)
    },
  },
};
</script>

