<style>
.login {
  width: 100vw;
  height: 100vh;
}
</style>
<template>
  <div class="login">
    <div class="bg-white container-3xl overflow-hidden">
      <div class="row">
        <div class="col bg-success-gradient position-relative">
          <div class="row justify-content-center align-items-center">
            <div class="col-lg-7 col-10">
              <img src="img/TKBtv-LOGO.svg" alt="TKBTV雲端函授" class="pb-3" />

              <div class="form-wrap rounded-3">
                <div class="tabs">
                  <p class="login-tab title">
                    <a
                      draggable="false"
                      class="text-decoration-none title-leftRound"
                      :class="{ active: inLoginArea }"
                      href="#login-tab-content"
                      @click="toLogin"
                      >雲端課程</a
                    >
                    <!--tab1 -->
                  </p>
                  <p class="signup-tab title title-rightRound">
                    <a
                      draggable="false"
                      class="text-decoration-none"
                      :class="{ active: !inLoginArea }"
                      href="#signup-tab-content"
                      @click="toQrcode"
                      >補課系統</a
                    >
                  </p>
                  <!--tab2 -->
                </div>
                <div class="tabs-content">
                  <keep-alive>
                    <component
                      :is="area"
                      @toTakoDownload="toTakoDownload"
                      @toQrcode="toQrcode"
                      @changePage="changePage"
                      @callWarn="callWarn"
                    ></component>
                  </keep-alive>
                </div>
                <!--.tabs-content-->
              </div>
              <!--.form-wrap-->
            </div>
          </div>
          <div
            class="
              info_content_item
              bg-darkSuccess
              d-flex
              justify-content-center
            "
          >
            <i class="fas fa-wrench align-self-center"></i>
            <div class="info_content_item_box">
              <div class="info_content_item_box_title">
                每週二<span>08:30 ~ 10:00</span><br />
                系統維護時間無法使用服務。若有不便，敬請見諒
              </div>
            </div>
            <p>
              <a>{{ app_version }}</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import LoginArea from "./LoginArea.vue";
import QRcodeArea from "./QRcodeArea.vue";
import TakoDownloadArea from "./TakoDownloadArea.vue";
import { ipcRenderer } from "electron";
export default {
  data() {
    return {
      area: "login-area",
      inLoginArea: true,
      isRemember: false,
      app_version: 'v'+ipcRenderer.sendSync("getVersion"),
    };
  },
  methods: {
    toLogin() {
      this.area = "login-area";
      this.inLoginArea = true;
    },
    toQrcode() {
      this.area = "q-rcode-area";
      this.inLoginArea = false;
    },
    toTakoDownload() {
      this.area = "tako-download-area";
      this.inLoginArea = false;
    },
    changePage(data) {
      console.log("LoginPage changePage" + data);
      this.$emit("changePage", data);
    },
    callWarn(data) {
      this.$emit("callWarn", data);
    },
  },
  components: {
    LoginArea,
    QRcodeArea,
    TakoDownloadArea,
  },
  mounted() {
  },
};
</script>
